<template>
  <v-sheet :elevation="8" class="pa-4">
    <div>
      <v-range-slider
        v-model="range"
        :min="1"       
        :max="9" 
        height="100"
        thumb-label="always"
      ></v-range-slider>

      Sum: {{ sum }}
    </div>
  </v-sheet>
</template>

<script>
  export default {
    data() {
      return {
        range: [1, 9],
      };
    },

    computed: {
      sum() {
        let sum = 0;

        for (var i = this.range[0]; i <= this.range[1]; i++) {
          sum += i;
        }

        return sum;
      }
    }
  }
</script>
