<script>
  import text_transform from './components/text-transform/Text-transform.vue';
  import minesweeper from './components/minesweeper/Minesweeper.vue';
  import sudoku from './components/sudoku/Sudoku.vue';
  import solitaire from './components/solitaire/Solitaire.vue';
  import set from './components/set/Set.vue';
  import cribbage from './components/cribbage/Cribbage.vue';
  import fileManager from './components/file-manager/FileManager.vue';
  import csvColumnInjector from './components/csv-column-injector/Csv-column-injector.vue';
  import sequenceAdder from './components/sequence-adder/SequenceAdder.vue';

  export default {
    components: {
      "text-transform": text_transform,
      minesweeper: minesweeper,
      sudoku: sudoku,
      solitaire: solitaire,
      set: set,
      cribbage: cribbage,
      "file-manager": fileManager,
      "csv-column-injector": csvColumnInjector,
      "sequence-adder": sequenceAdder,
    },

    data: () => ({
      primaryDrawer: {
        model: null,
        type: 'default (no property)',
      },

      pages: [
        { name: "Text Transform", icon: "mdi-format-text-rotation-angle-down", comp: "text-transform" },
        { name: "Minesweeper", icon: "mdi-mine", comp: "minesweeper" },
        { name: "Sudoku", icon: "mdi-grid", comp: "sudoku" },
        { name: "CSV Column Injector", icon: "mdi-needle", comp: "csv-column-injector" },
        //{ name: "File Manager", icon: "mdi-folder-multiple", comp: "file-manager" },
        //{ name: "Solitaire", icon: "mdi-cards", comp: "solitaire" },
        { name: "Set", icon: "mdi-layers-triple-outline", comp: "set" },
        { name: "Cribbage", icon: "mdi-cards", comp: "cribbage" },
        { name: "Sequence Adder", icon: "mdi-plus", comp: "sequence-adder" },
      ],

      page: 4
    }),

    watch: {
      page() {
        this.page = this.page || 0;
      }
    },

    created() {
      this.$vuetify.theme.dark = true;
    },

    beforeMount() {
      document.location.search.substr(1).split("&")
        .map(s => s.split("="))
        .filter(keyAndValue => keyAndValue[0] == "app")
        .map(keyAndValue => keyAndValue[1])
        .forEach(value => {
          let page = this.pages
            .map(p => p.name.toLowerCase())
            .indexOf(value.toLowerCase());

          if (page > -1) {
            this.page = page;
          }
        })

    },
  }
</script>

<template>
  <v-app>
    <v-navigation-drawer v-model="primaryDrawer.model" :clipped="true" app overflow>
      <v-list nav shaped>
        <v-list-item-group v-model="page">
          <v-list-item v-for="(page, index) in pages" :key="index">
            <v-list-item-icon>
              <v-icon>{{ page.icon }}</v-icon>
            </v-list-item-icon>

            <v-list-item-content>
              <v-list-item-title>{{ page.name }}</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </v-list-item-group>
      </v-list>
    </v-navigation-drawer>

    <v-app-bar :clipped-left="true" app>
      <v-app-bar-nav-icon @click.stop="primaryDrawer.model = !primaryDrawer.model" />
      <v-toolbar-title>{ ryan.deba }</v-toolbar-title>
    </v-app-bar>

    <v-main>
      <v-container fluid>
        <v-row align="center" justify="center">
          <v-col cols="12" style="max-width: 1400px;">
            <component :is="pages[page].comp"></component>
          </v-col>
        </v-row>
      </v-container>
    </v-main>
  </v-app>
</template>
