<script>
  import card from './Card.vue';
  import handCombos from './HandCombos.vue';
  import handScores from './HandScores.vue';

  let suits = ["Clubs", "Hearts", "Spades", "Diamonds"];
  let values = ["Ace", "2", "3", "4", "5", "6", "7", "8", "9", "10", "Jack", "Queen", "King"];

  export default {
    components: {
      card: card,
      "hand-combos": handCombos,
      "hand-scores": handScores,
    },

    data() {
      return {
        cards: [],

        hand: [],
      };
    },

    computed: {
      selectedCards() {
        return this.hand.filter(c => c.selected);
      },

      debugHand() {
        return [
          {suit: "Clubs", value: "2"},
          {suit: "Hearts", value: "3"},
          {suit: "Clubs", value: "3"},
          {suit: "Clubs", value: "4"},
          {suit: "Clubs", value: "5"},
        ];
      }
    },

    methods: {
      shuffleCards() {
        this.cards.sort(() => {
          return Math.random() < Math.random() ? 1 : -1;
        });
      },

      sortHand() {
        this.hand.sort((a, b) => {
          return values.indexOf(a.value) > values.indexOf(b.value) ? 1 : -1;
        });
      },

      returnHand() {
        this.cards = this.cards
          .concat(this.hand)
          .map(c => { return {suit: c.suit, value: c.value}; });
        this.hand = [];
      },

      deal() {
        this.returnHand();

        this.shuffleCards();

        for (var i = 0; i < 6; i++) {
          this.hand = this.hand.concat(this.cards.pop());
        }

        //this.sortHand();
      },

      selectCard(card) {
        if (!card.selected && this.selectedCards.length == 2) {
          return;
        }
        this.$set(card, "selected", !card.selected);
      },
    },

    beforeMount() {
      suits.forEach(s => {
        values.forEach(v => {
          this.cards.push({suit: s, value: v});
        });
      });

      this.deal();
    },
  }
</script>

<template>
  <div>
    <!--
    <div class="hand">
      <div
        v-for="card in debugHand"
        :key="card.suit + card.value"
      >
        <card v-bind="card"></card>
      </div>
    </div>

    <hand-scores v-bind="{hand: debugHand}"></hand-scores>
    -->

    <v-btn color="green" @click="deal">
      Deal
    </v-btn>

    <v-btn color="blue" v-if="selectedCards.length == 2">
      Check
    </v-btn>

    <div class="hand">
      <div
        v-for="card in hand"
        :key="card.suit + card.value"
        @click="selectCard(card)"
        :class="{'selected': card.selected}"
      >
        <card v-bind="card"></card>
      </div>
    </div>

    <hr>

    <hand-combos v-model="hand"></hand-combos>
  </div>
</template>

<style lang="scss" scoped>
  .hand {
    display: flex;
    padding-bottom: 20px;
  }
    .hand > * {
      transition: 0.2s all;
    }

    .hand > * + * {
      margin-left: 20px;
    }

    .hand .selected {
      margin-top: 20px;
    }
</style>
