const MAX_UNDO_STACK_SIZE = 25;
const MS_BEFORE_COMMIT_SNAPSHOT = 200;

var undoStack = [];
var redoStack = [];
var currentState = undefined;
var paused = false;
var snapshotTimeout = undefined;

export default {
  undo() {
    if (undoStack.length == 0) {
      return;
    }

    paused = true;

    currentState && redoStack.push(currentState);
    currentState = undoStack.pop();

    setTimeout(function() {
      paused = false;
    }, 0);

    return currentState;
  },

  redo() {
    if (redoStack.length == 0) {
      return;
    }

    paused = true;

    currentState && undoStack.push(currentState);
    currentState = redoStack.pop();

    setTimeout(function() {
      paused = false;
    }, 0);

    return currentState;
  },

  snapshot(state) {
    if (paused) {
      return;
    }

    clearTimeout(snapshotTimeout);

    snapshotTimeout = setTimeout(commitSnapshot, MS_BEFORE_COMMIT_SNAPSHOT);

    function commitSnapshot() {
      if (
        Boolean(currentState)
        && undoStack.length == 0
        || JSON.stringify(currentState) != JSON.stringify(undoStack[undoStack.length - 1])
      ) {
        currentState && undoStack.push(currentState);
      }

      currentState = JSON.parse(JSON.stringify(state));
      redoStack = [];

      undoStack.splice(0, undoStack.length - MAX_UNDO_STACK_SIZE);
    }
  },

  reset() {
    clearTimeout(snapshotTimeout);
    currentState = undefined;
    undoStack = [];
    redoStack = [];
  }
}
