<template>
  <div>
    <v-hover v-slot:default="{ hover }" close-delay="200">
      <v-sheet elevation="5">
        <v-chip-group v-model="chipIndex" column class="pa-4">
          <template v-for="(fn, n) in fns">
            <fn
              v-bind="fn"
              :key="n"
              :input="results[n]"
              v-on:update:output="$set(results, n + 1, $event)"
              v-on:delete="fns.splice(n, 1)"
            ></fn>

            <v-chip :key="'add_post' + n" @click="addFn(n + 1)">
              <v-fade-transition mode="out-in">
                <v-icon :key="hover">
                  <template v-if="hover">mdi-plus</template>
                  <template v-else>mdi-chevron-right</template>
                </v-icon>
              </v-fade-transition>
            </v-chip>
          </template>
        </v-chip-group>

        <v-expand-transition mode="out-in">
          <edit-fn
            v-if="Number.isInteger(fnIndex)"
            :key="fnIndex"
            :fn-string="fns[fnIndex].fnString"
            :args-string="fns[fnIndex].argsString"
            @update="Object.assign(fns[fnIndex], $event); fnIndex = undefined; $emit('input', fns)"
          ></edit-fn>
        </v-expand-transition>
      </v-sheet>
    </v-hover>

    
  </div>
</template>

<script>
  import fn from './Fn.vue';
  import editFn from './EditFn.vue';

  export default {
    components: {
      "fn": fn,
      "edit-fn": editFn,
    },

    props: {
      value: {
        type: Array,
        required: true
      },

      textInput: {
        type: String,
        required: true
      },
    },

    data() {
      return {
        fns: [],
        results: [],

        chipIndex: undefined,
        fnIndex: undefined,
      };
    },

    watch: {
      value: {
        handler() { this.fns = this.value; },
        immediate: true
      },

      textInput: {
        handler() { this.$set(this.results, 0, this.textInput); },
        immediate: true
      },

      results: {
        handler() {
          this.$emit("update:output", this.results.slice(-1));
        },
        deep: true
      },

      chipIndex() {
        if (this.chipIndex == undefined) {
          return;
        }

        if (this.chipIndex % 2 == 0) {
          this.fnIndex = this.chipIndex / 2;
        } else {
          this.chipIndex = undefined;
        }
      },

      fns() {
        this.results = [this.textInput];
      }
    },

    methods: {
      addFn(index) {
        this.fns.splice(index, 0, {fnString: "", argsString: ``});
        this.chipIndex = undefined;
      }
    }
  };
</script>

<style lang="scss" scoped>
  .code {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    max-width: 100%;
  }
</style>
