<template>
  <v-card
    :class="classes"
    :hover="!disabled"
    outlined
    :raised="!value"
    :shaped="disabled"
    @click="$emit('click')"
    @click.right.prevent="$emit('click:right')"
    @keydown="onKeyDown"
    :disabled="disabled"
  >
    <v-icon
      v-if="Boolean(value)"
      :color="iconColor"
      large
    >
      mdi-numeric-{{ value }}
    </v-icon>

    <template v-else>
     <v-icon
       v-for="i in 9"
       :key="i"
       :color="iconColor"
       :size="19"
      >
        <template v-if="notes[i]">
          mdi-numeric-{{ i }}
        </template>
      </v-icon> 
    </template>
  </v-card>
</template>

<script>
  export default {
    props: {
      value: {
        required: true
      },

      highlighted: {
        type: Boolean,
        default: false
      },

      disabled: {
        type: Boolean,
        default: false
      },

      invalid: {
        type: Boolean,
        default: false
      },

      notes: {
        type: Object,
        default() {
          return {};
        }
      },
    },

    data() {
      return {
      };
    },

    computed: {
      classes() {
        var classes = ["_cell"];

        if (!this.value) {
          classes.push("_cell--notes");
        } else if (this.invalid) {
          classes.push("red darken-4");
        }

        if (this.highlighted && !this.invalid) {
          classes.push("grey");

          if (this.value) {
            classes.push("lighten-3");
          } else {
            classes.push("lighten-1");
          }
        }

        return classes.join(" ");
      },

      iconColor() {
        var classes = [];

        if (this.value) {
          classes.push("teal");
        } else {
          classes.push("blue-grey");
        }

        if (this.highlighted && !this.invalid) {
          classes.push("darken-5");
        } else {
          classes.push("lighten-5");
        }

        return classes.join(" ");
      }
    },

    methods: {
      onKeyDown(e) {
        var digits = new Array(9).fill("").map((a, i) => i + 1).map(String);

        if (digits.indexOf(e.key) > -1) {
          this.$emit("input", e.key);
        } else if ([" ", "Backspace", "Delete"].indexOf(e.key) > -1) {
          this.$emit("input", "");
        }
      }
    }
  };
</script>

<style lang="scss" scoped>
  ._cell {
    display: flex;
    justify-content: center;
  }

  ._cell--notes {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
  }
</style>
