<template>
  <v-chip :color="color" class="fn" close v-on:click:close="$emit('delete')">
    .{{ fnString }}({{ argsString }})
  </v-chip>
</template>

<script>
export default {
  props: {
    fnString: {
      type: String,
      required: true
    },

    argsString: {
      type: String,
      required: true
    },

    input: {},
  },

  data() {
    return {
      processing: false,
      output: undefined,
      promise: Promise.resolve(),
    };
  },

  computed: {
    color() {
      if (this.processing) {
        return "grey";
      }

      if (this.output === undefined) {
        return "red";
      }

      return "green";
    },

    friendlyOutput() {
      return String(this.output).replace(/\n/g, "\\n");
    },
  },

  watch: {
    input() {
      this.updateOutput();
    },

    output() {
      this.$emit("update:output", this.output);
    },

    processing() {
      this.$emit("update:processing", this.processing);
    }
  },

  methods: {
    updateOutput() {
      var self = this;

      if (self.processing) {
        return;
      }

      self.promise
        .then(() => {
          self.processing = true;
        })
        .then(() => new Promise(r => setTimeout(r, 200)))
        .then(() => {
          try {
            eval(`self.output = self.input["${self.fnString}"](${self.argsString})`);
          } catch (e) {
            self.output = undefined;
          }
        })
        .finally(() => {
          self.processing = false;
        });
      
    },
  }
};
</script>

<style lang="scss" scoped>
  .fn {
    transition: all 0.2s;
  }
</style>
