<template>
  <v-container>
    <v-row>
      <v-col cols="12">
        Step 1: Select Authority File
        <file-upload v-model="authorityData"></file-upload>
      </v-col>

      <template v-if="authorityData.length > 0">
        <v-col cols="12">
          <v-divider></v-divider>
        </v-col>

        <v-col cols="12">
          Step 2: Select File(s) to Inject
          <file-upload v-model="otherData" :multiple="true" v-on:ready="prepFileForDownload"></file-upload>
        </v-col>

        <template v-if="Object.keys(links).length > 0">
          <v-col cols="12">
            <v-divider></v-divider>
          </v-col>

          <v-col cols="12">
            Step 3: Download Injected file

            <div style="display: grid; grid-gap: 5px;">
              <div
                v-for="key in Object.keys(links)"
                :key="key"
              >
                <a :href="links[key]" :download="key" v-on:click="$set(downloadedLinks, key, true)">
                  {{ key }}
                </a>

                <v-icon v-if="downloadedLinks[key]">mdi-check</v-icon>
              </div>
            </div>
          </v-col>
        </template>
      </template>
    </v-row>
  </v-container>
</template>

<script>
  import Papa from 'papaparse';
  import FileUpload from './FileUpload.vue';

  export default {
    components: {
      "file-upload": FileUpload,
    },

    data() {
      return {
        authorityData: [],
        otherData: [],
        links: {},
        downloadedLinks: {},
      };
    },

    computed: {
      domainBusinessUnits() {
        var map = {};

        this.authorityData
          .filter(d => d.length > 1)
          .forEach(d => {
            map[d[0]] = d[1];
          });

        return map;
      },
    },

    methods: {
      prepFileForDownload(fileData) {
        var injectedData = fileData.data.map((d, i) => {
          if (d.length > 1) {
            var businessUnit = "Business Unit";

            if (i > 0) {
              businessUnit = this.domainBusinessUnits[d[0]] || "*** BUSINESS UNIT NOT FOUND ***";
            }

            d.splice(1, 0, businessUnit);
          }

          return d;
        });

        if (injectedData.length > 0) {
          injectedData = encodeURI("data:text/csv;charset=utf-8," + Papa.unparse(injectedData));
        }

        this.$set(this.links, fileData.name, injectedData);
      },
    },
  };
</script>
