<template>
  <div>
    <button type="button" @click="onClick" v-if="name.length > 0">
      <v-icon>
        {{ expanded ? 'mdi-folder-open' : 'mdi-folder' }}
      </v-icon>

      {{ name }}
    </button>

    <div v-if="expanded" style="padding-left: 20px;">
      <folders
        v-for="(folder, index) in folders"
        :key="index"
        v-bind="folder"
        @select="$emit('select', name.replace('/', '') + '/' + $event)"
      ></folders>
    </div>
  </div>
</template>

<script>
  export default {
    name: "folders",

    props: {
      name: {
        type: String,
        required: true
      },

      folders: {
        type: Array,
        required: true
      },
    },

    data() {
      return {
        expanded: false
      };
    },

    methods: {
      onClick() {
        this.expanded = !this.expanded
        this.$emit("select", this.name);
      },
    }
  }
</script>

