<template>
  <div class="selectable-values">
    <opt
      v-for="selectableValue in selectableValues"
      :key="selectableValue.value"
      @click="$emit('input', selectableValue.value)"
      :tooltip="selectableValue.remaining + ' remaining'"
      :cardClass="value == selectableValue.value ? 'primary' : ''"
    >
      <v-icon large>mdi-numeric-{{ selectableValue.value }}</v-icon>
      <span class="font-weight-thin">{{ selectableValue.remaining }}</span>
    </opt>

    <opt
      @click="$emit('input', '')"
      tooltip="Erase"
      :cardClass="value == '' ? 'primary' : ''"
      icon="mdi-eraser"
    >
    </opt>

    <opt
      @click="$emit('update:notes-mode', !notesMode)"
      icon="mdi-pencil"
      tooltip="Notes (right-click)"
      :cardClass="notesMode ? 'primary' : ''"
    >
    </opt>

    <opt
      @click="$emit('update:show-invalid', !showInvalid)"
      icon="mdi-exclamation-thick"
      tooltip="Show Incorrect"
      :cardClass="showInvalid ? 'primary' : ''"
    >
    </opt>

    <opt
      @click="$emit('undo')"
      icon="mdi-undo"
      tooltip="Undo"
    >
    </opt>

    <opt
      @click="$emit('redo')"
      icon="mdi-redo"
      tooltip="Redo"
    >
    </opt>
  </div>
</template>

<script>
  import menuOption from "./MenuOption.vue";

  export default {
    components: {
      opt: menuOption,
    },

    props: {
      value: {
      },

      notesMode: {
        type: Boolean,
      },

      showInvalid: {
        type: Boolean,
      },

      puzzle: {
        type: Array
      }
    },

    data() {
      return {
        selectedValueIndex: 0
      };
    },

    computed: {
      selectableValues() {
        return new Array(9)
          .fill("")
          .map((a, i) => i + 1)
          .map(i => {
            return {
              value: i,
              remaining: Math.max(9 - this.puzzle.filter(cell => cell == i).length, 0)
            }
          });
      },
    },

    watch: {
      value: {
        handler() {
          this.input = this.value;
        },
        immediate: true
      },

      input() {
        this.$emit("input", this.input);
      }
    }
  };
</script>

<style lang="scss" scoped>
  .selectable-values {
    display: grid;
    grid-template-columns: 80px 80px 80px;
    grid-template-rows: 80px 80px 80px 80px 80px;
    grid-gap: 3px;
  }
</style>
