<template>
  <v-row class="pa-4">
    <v-col cols="3" class="d-flex align-center">
      <code class="black green--text">.</code>

      <v-select
        v-model="input_fnString"
        :items="fnStrings"
        :hide-details="true"
        solo
      ></v-select>
    </v-col>

    <v-col cols="9" class="d-flex align-center">
      <code class="black green--text">(</code>

      <v-text-field
        v-model="input_argsString"
        :hide-details="true"
        solo
      ></v-text-field>

      <code class="black green--text">)</code>
    </v-col>

    <v-col cols="12" class="d-flex justify-end">
      <v-btn color="green" v-on:click="save">
        <v-icon>mdi-content-save</v-icon>
      </v-btn>
    </v-col>
  </v-row>
</template>

<script>
  import fn from './Fn.vue';

  export default {
    extends: fn,

    data() {
      return {
        input_fnString: "",
        input_argsString: "",

        fnStrings: ["replace", "split", "join", "map", "filter"],
      };
    },

    watch: {
      fnString: {
        handler() {
          this.input_fnString = this.fnString;
        },
        immediate: true,
      },

      argsString: {
        handler() {
          this.input_argsString = this.argsString;
        },
        immediate: true,
      }
    },

    methods: {
      save() {
        this.$emit("update", {fnString: this.input_fnString, argsString: this.input_argsString});
      }
    }
  };
</script>

<style lang="scss" scoped>
  .code {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    max-width: 100%;
  }
</style>
