<template>
  <v-speed-dial direction="bottom" v-model="fab">
    <template v-slot:activator>
      <v-btn v-model="fab" :color="speedDialActivatorClasses" dark fab>
        <v-icon v-if="fab">mdi-close</v-icon>
        <v-icon v-else>mdi-plus</v-icon>
      </v-btn>
    </template>

    <v-btn fab dark small color="green" v-on:click="$emit('input', 'easy')">
      <v-icon>mdi-signal-cellular-1</v-icon>
    </v-btn>

    <v-btn fab dark small color="orange" v-on:click="$emit('input', 'medium')">
      <v-icon>mdi-signal-cellular-2</v-icon>
    </v-btn>

    <v-btn fab dark small color="red" v-on:click="$emit('input', 'hard')">
      <v-icon>mdi-signal-cellular-3</v-icon>
    </v-btn>
  </v-speed-dial>
</template>

<script>
  export default {
    props: {
      value: {
        type: String,
        required: true
      }
    },

    data() {
      return {
        fab: undefined
      };
    },

    computed: {
      speedDialActivatorClasses() {
        if (this.value == "easy") {
          return "green";
        } 
        if (this.value == "medium") {
          return "orange";
        }
        return "red";
      }
    }
  };
</script>
