<script>
  import card from './Card.vue';
  import fns from '../util/fns.js';

  const colors = ["red", "purple", "green"];
  const counts = [1, 2, 3];
  const shapes = ["oval", "squiggle", "diamond"];
  const patterns = ["hollow", "filled", "lines"];

  const isSet = cards => {
    let props = {};

    if (cards.length != 3) {
      return false;
    }

    cards.forEach(c => {
      ["color", "count", "shape", "pattern"].forEach(p => {
        props[c[p]] =  props[c[p]] || 0;
        props[c[p]] += 1;
      })
    });

    return Object.keys(props).filter(p => props[p] == 2).length == 0;
  };

  export default {
    components: {
      card: card
    },

    data() {
      return {
        cards: [],
        board: [],
        selectedIndeces: [],

        snackbarSet: false,
        snackbarNotSet: false,

        debug: false,
      };
    },

    computed: {
      sets() {
        return fns.generateCombinations(this.board, 3)
          .filter(isSet);
      }
    },

    watch: {
      snackbarSet() {
        if (this.snackbarSet) {
          setTimeout(() => {
            this.snackbarSet = false;
          }, 2000);
        }
      },

      snackbarNotSet() {
        if (this.snackbarNotSet) {
          setTimeout(() => {
            this.snackbarNotSet = false;
          }, 2000);
        }
      }

    },

    methods: {
      newGame() {
        let cards = [];

        colors.forEach(color => {
          counts.forEach(count => {
            shapes.forEach(shape => {
              patterns.forEach(pattern => {
                cards.push({ color: color, count: count, shape: shape, pattern: pattern });
              });
            });
          });
        });

        cards.sort(() => {
          return Math.random() < Math.random() ? 1 : -1;
        });

        this.$set(this, "cards", cards);
        this.board = [];

        this.fillBoard();
      },

      checkSelected() {
        if (isSet([
          this.board[this.selectedIndeces[0]],
          this.board[this.selectedIndeces[1]],
          this.board[this.selectedIndeces[2]],
        ])) {
          this.snackbarSet = true;

          this.clearAndReplaceSelected();
        } else {
          this.snackbarNotSet = true;
        }
      },

      fillBoard() {
        while (this.board.length < 12 && this.cards.length > 0) {
          this.board.push(this.cards.pop());
        }
      },

      clearAndReplaceSelected() {
        if (this.cards.length < 3) {
          throw("TODO: not enough cards");
        }

        this.selectedIndeces.forEach(i => {
          this.board.splice(i, 1, this.cards.pop());
        });

        this.selectedIndeces = [];
      },

      deal3() {
        for (let i = 0; i < 3 && this.cards.length > 0; i++) {
          this.board.push(this.cards.pop());
        }
      },

      toggleSelectedIndex(index) {
        if (this.selectedIndeces.indexOf(index) > -1) {
          this.selectedIndeces = this.selectedIndeces.filter(i => i != index);
        } else {
          this.selectedIndeces.push(index);
        }

        if (this.selectedIndeces.length == 3) {
          this.checkSelected();
        }
      },
    },

    beforeMount() {
      this.newGame();
    }
  }
</script>

<template>
  <div>
    <svg width="0" height="0">
      <pattern id="striped-red" patternUnits="userSpaceOnUse" width="4" height="4"><path d="M-1,1 H5" style="stroke:#e74c3c; stroke-width:1" /></pattern>
      <pattern id="striped-green" patternUnits="userSpaceOnUse" width="4" height="4"><path d="M-1,1 H5" style="stroke:#27ae60; stroke-width:1" /></pattern>
      <pattern id="striped-purple" patternUnits="userSpaceOnUse" width="4" height="4"><path d="M-1,1 H5" style="stroke:#8e44ad; stroke-width:1" /></pattern>
    </svg>

    <div style="display: flex; justify-content: space-between; align-items: center;">
      <v-btn color="primary" v-on:click="newGame">New Game</v-btn>

      <v-tooltip bottom>
        <template v-slot:activator="{ on, attrs }">
          <v-btn color="primary" v-bind="attrs" v-on="on" v-on:click="deal3">Add 3 Cards</v-btn>
        </template>
        <span>
          Sets Available: {{ sets.length }}
        </span>
      </v-tooltip>

      <span>
        Cards: {{ cards.length }}
      </span>

      <v-checkbox v-model="debug" label="Debug"></v-checkbox>
    </div>

    <hr style="margin: 10px 0;">

    <slide-y-up-transition group mode="out-in" class="board">
      <card
        v-for="(card, index) in board"
        v-bind="card"
        :selected="selectedIndeces.indexOf(index) > -1"
        :key="JSON.stringify(card)"
        v-on:toggle="toggleSelectedIndex(index)"
      ></card>
    </slide-y-up-transition>

    <template v-if="debug">
      <hr style="margin: 10px 0;">
      <strong>Sets</strong>

      <div style="display: grid; grid-gap: 5px;">
        <div class="board" v-for="(s, i) in sets" :key="'set' + i" style="">
          <card v-for="c in s" v-bind="c" :key="i + JSON.stringify(c)"></card>
        </div>
      </div>
    </template>

    <v-snackbar v-model="snackbarSet" color="green">Set!</v-snackbar>
    <v-snackbar v-model="snackbarNotSet" color="red">Not a Set!</v-snackbar>
  </div>
</template>

<style lang="scss" scoped>
  .board {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 10px;
  }
</style>
