<script>
  import fns from '../util/fns.js';

  export default {
    props: {
      hand: {
        type: Array,
        required: true
      },
    },

    computed: {
      formattedHand() {
          return this.hand
          .map(c => {
            let runValue = 0;

            if (Number.isInteger(Number.parseInt(c.value))) {
              runValue = Number.parseInt(c.value);
            } else if (c.value.toLowerCase().substr(0, 1) == "a") {
              runValue = 1;
            } else if (c.value.toLowerCase().substr(0, 1) == "j") {
              runValue = 11;
            } else if (c.value.toLowerCase().substr(0, 1) == "q") {
              runValue = 12;
            } else if (c.value.toLowerCase().substr(0, 1) == "k") {
              runValue = 13;
            }

            if (runValue == 0) {
              throw("could not determine numeric value", c);
            }

            return {
              ...c,
              runValue: runValue,
              fifteenValue: runValue > 10 ? 10 : runValue
            }
          })
          .sort((a, b) => { return a.runValue - b.runValue; });
      },

      formattedHandCombos() {
        let combos = [];

        for (let i = 1; i <= this.formattedHand.length; i++) {
          combos.push(fns.generateCombinations(this.formattedHand, i));
        }

        return combos.flat();
      },

      valueCounts() {
        let valueCounts = {};

        this.hand.forEach(c => {
          valueCounts[c.value] = valueCounts[c.value] || 0;
          valueCounts[c.value]++;
        });

        return valueCounts;
      },

      fifteens() {
        return this.formattedHandCombos
          .filter(h => {
            let sum = h
              .map(c => c.fifteenValue)
              .reduce((a, b) => a + b, 0);

            return sum == 15;
          });
      },

      pairs() {
        return this.formattedHandCombos
          .filter(h => h.length == 2)
          .filter(h => {
            return h.filter(c => c.value == h[0].value).length == h.length;
          });
      },

      runs() {
        return JSON.parse(JSON.stringify(this.formattedHandCombos))
          .filter(h => h.length > 2)
          .map(h => {
            return h.sort((a, b) => a.runValue - b.runValue);
          })
          .filter(h => {
            let value = h[0].runValue;

            for (let i = 1; i < h.length; i++) {
              if (++value != h[i].runValue) {
                return false;
              }
            }

            return true;
          })
          .filter((h, index, arr) => {
            let handJSON = JSON.stringify(h).split("").slice(1, -1).join("");

            return !arr
              .map(JSON.stringify)
              .map(s => {
                return s.split("").slice(1, -1).join("");
              })
              .find((a, i) => {
                return i != index && a.indexOf(handJSON) > -1;
              });
          });
      },

      flushes() {
        let suitCounts = {};

        this.formattedHand.forEach(c => {
          suitCounts[c.suit] = suitCounts[c.suit] || 0;
          suitCounts[c.suit]++;
        });

        return Object.keys(suitCounts)
          .filter(k => {
            return suitCounts[k] == 4;
          })
          .map(suit => {
            return this.formattedHand.filter(c => c.suit == suit);
          });
      },


      score() {
        return (this.fifteens.length * 2)
          + (this.pairs.length * 2)
          + (this.runs.reduce((a, b) => a + b.length, 0))
          + ((this.flushes[0] || []).length);
      },
    },

  }
</script>

<template>
  <div>
    <div>
      <strong>Fifteens:</strong> {{ fifteens.length }}
      <div v-for="(combo, index) in fifteens" :key="index">
        {{ combo.map(c => {return c.value + " of " + c.suit; }) }}
      </div>
    </div>

    <div>
      <strong>Pairs:</strong> {{ pairs.length }}
      <div v-for="(combo, index) in pairs" :key="index">
        {{ combo.map(c => {return c.value + " of " + c.suit; }) }}
      </div>
    </div>

    <div>
      <strong>Runs:</strong> {{ runs.length }}
      <div v-for="(combo, index) in runs" :key="index">
        {{ combo.map(c => {return c.value + " of " + c.suit; }) }}
      </div>
    </div>

    <div>
      <strong>Flushes:</strong> {{ flushes.length }}
      <div v-for="(combo, index) in flushes" :key="index">
        {{ combo.map(c => {return c.value + " of " + c.suit; }) }}
      </div>
    </div>

    <div>
      <strong>Score:</strong> {{ score }}
    </div>
  </div>
</template>
