<script>
  import handScores from './HandScores.vue';
  import fns from '../util/fns.js';

  export default {
    components: {
      "hand-scores": handScores,
    },

    props: {
      value: {
        type: Array,
        required: true
      },
    },

    data() {
      return {
        input: [],
      };
    },

    computed: {
      combinations() {
        return fns.generateCombinations(this.input, 4);
      }
    },

    watch: {
      value: {
        handler() {
          this.input = JSON.parse(JSON.stringify(this.value));
        },
        immediate: true
      }
    },
  }
</script>

<template>
  <div>
    <ol>
      <li
        v-for="(combo, index) in combinations"
        :key="index"
      >
        {{ combo.map(c => { return `${c.value} of ${c.suit}`; }).join(", ") }}

        <hand-scores :hand="combo"></hand-scores>
      </li>
    </ol>
  </div>
</template>
