function createRandomSolution() {
  return new Promise((resolve, reject) => {
    var solution = undefined;

    while (!solution) {
      try {
        solution = _createRandomSolution();
      } catch (e) {
        if (!e.type || e.type.indexOf("createRandomSolution") == -1) {
          reject(e);
        }
      }
    }

    resolve(solution);
  });

  function _createRandomSolution(solution) {
    solution = solution || new Array(81).fill("");

    while (solution.indexOf("") > -1) {
      var index = solution.indexOf("");

      var possibleValues = _.shuffle(getPossibleValues(solution, index));

      if (possibleValues.length == 0) {
        throw({type: "createRandomSolution:no possible value for index", details: {index: index, solution: solution}});
      }

      solution[index] = possibleValues.pop();
      solution = _createRandomSolution(solution) || solution;
    }

    return solution;
  }
}

function createPuzzleFromSolution(solution, difficulty) {
  difficulty = difficulty || "hard";
  difficulty = ["easy", "medium", "hard"].indexOf(difficulty) > -1 ? difficulty : "hard";

  var indeces = solution.map((a, i) => i);
  indeces = _.shuffle(indeces);

  var result = JSON.parse(JSON.stringify(solution));

  while (indeces.length > 0) {
    var index = indeces.pop();
    var testPuzzle = JSON.parse(JSON.stringify(result));
    testPuzzle[index] = "";

    if (getSolutionsForPuzzle(testPuzzle).length == 1) {
      result = testPuzzle;
      continue;
    }
  }

  var cellsToRepopulate = 0;
  if (difficulty == "easy") {
    cellsToRepopulate = 30;
  } else if (difficulty == "medium") {
    cellsToRepopulate = 15;
  }

  _.shuffle(result.map((c, i) => c == "" ? i : ""))
    .filter(i => i != "")
    .slice(0, cellsToRepopulate)
    .forEach(i => {
      result[i] = solution[i];
    });

  return result;
}

function getOtherIndeces(index) {
  return rows.find(s => s.indexOf(index) > -1)
    .concat(columns.find(s => s.indexOf(index) > -1))
    .concat(squares.find(s => s.indexOf(index) > -1))
    .filter(i => i != index);
}

function getPossibleValues(puzzle, index) {
  var valueCounts = {};

  new Array(9).fill("").map((a, i) => i + 1).forEach(i => { valueCounts[i] = 0; })

  getOtherIndeces(index)
    .forEach(i => {
      valueCounts[puzzle[i]]++;
    });

  return Object.keys(valueCounts)
    .filter(key => {
      return valueCounts[key] == 0;
    })
}

export default {
  createRandomSolution: createRandomSolution,
  createPuzzleFromSolution: createPuzzleFromSolution,
  getSolutionsForPuzzle: getSolutionsForPuzzle,
}
