<template>
  <div class="files">
    <template v-if="loading">
      Loading...
    </template>

    <template v-else>
      <file
        v-for="(file, index) in files"
        :key="index"
        v-bind="file"
      ></file>
    </template>
  </div>
</template>

<script>
  import file from "./File.vue";

  export default {
    components: {
      file: file,
    },

    props: {
      path: {
        type: String,
        required: true
      }
    },

    data() {
      return {
        files: [],
        loading: false,
      };
    },

    watch: {
      path: {
        handler() {
          this.load();
        },
        immediate: true
      }
    },

    methods: {
      load() {
        this.loading = true;

        Promise.resolve()
          .then(() => {
            return new Promise(resolve => setTimeout(resolve, 1000));
          })
          .then(() => {
            // TOOD
            this.files = [
              {name: "dog.png"},
              {name: "dog (2).png"},
            ];
          })
          .finally(() => {
            this.loading = false;
          });
      }
    }
  }
</script>

<style lang="scss" scoped>
  .files {
    display: flex;
  }
</style>
