<template>
  <v-sheet :elevation="8" class="pa-4 sudoku">
    <v-overlay :value="loading">
      <v-progress-circular indeterminate size="128"></v-progress-circular>
    </v-overlay>

    <v-overlay :value="solved && !loading">
      <div class="title text-center">
        Solved!
      </div>

      <new-game :value="difficulty" v-on:input="newGame($event)"></new-game>
    </v-overlay>

    <div class="sudoku__header d-flex justify-space-between align-center">
      <div class="title">Sudoku</div>

      <div v-if="solved">
        Solved!
      </div>

      <new-game :value="difficulty" v-on:input="newGame($event)"></new-game>
    </div>

    <div class="sudoku__board" v-if="initialized">
      <cell
        v-for="(cell, n) in puzzle"
        :key="n"
        class="sudoku__cell"
        :value="cell"
        :notes="notes[n]"
        :highlighted="selectedValue > 0 && (cell == selectedValue || (cell == '' && notes[n][selectedValue]))"
        :invalid="showInvalid && Boolean(cell) && cell != solution[n]"
        :disabled="Boolean(originalPuzzle[n])"
        @click="setPuzzleIndexValue(n)"
        @click:right="setNotesIndexValue(n)"
        @input="setPuzzleIndexValue(n, $event)"
      ></cell>
    </div>

    <sudoku-menu
      class="sudoku__menu"
      v-if="initialized"
      v-model="selectedValue"
      :notes-mode.sync="notesMode"
      :show-invalid.sync="showInvalid"
      :puzzle="puzzle"
      @undo="undo"
      @redo="redo"
    ></sudoku-menu>
  </v-sheet>
</template>

<script>
  import cell from './Cell.vue';
  import fns from './Functions.js';
  import newGame from './NewGame.vue';
  import sudokuMenu from './Menu.vue';
  import historyManager from '../util/History.js';

  export default {
    components: {
      cell: cell,
      "new-game": newGame,
      "sudoku-menu": sudokuMenu,
    },

    data() {
      return {
        solution: [],
        puzzle: [],
        originalPuzzle: [],
        notes: [],
        loading: false,

        selectedValue: "1",

        notesMode: false,
        showInvalid: false,

        difficulty: "hard",
      };
    },

    computed: {
      selectableValues() {
        return new Array(9)
          .fill("")
          .map((a, i) => i + 1)
          .map(i => {
            return {
              value: i,
              remaining: Math.max(9 - this.puzzle.filter(cell => cell == i).length, 0)
            }
          })
          .concat([{value: ""}]);
      },

      solved() {
        return this.solution.length == 81 && JSON.stringify(this.solution) == JSON.stringify(this.puzzle);
      },

      initialized() {
        return !this.loading && this.solution.length == 81;
      }
    },

    watch: {
      puzzle() {
        this.saveStateToHistory();
      },

      notes() {
        this.saveStateToHistory();
      }
    },

    methods: {
      newGame(difficulty) {
        if (this.loading) {
          return;
        }

        return Promise.resolve()
          .then(() => {
            this.loading = true;
            this.difficulty = difficulty;
          })
          .then(() => new Promise(r => setTimeout(r, 250)))
          .then(fns.createRandomSolution)
          .then(solution => {
            this.solution = solution;
          })
          .then(() => {
            this.puzzle = fns.createPuzzleFromSolution(this.solution, this.difficulty);
            this.originalPuzzle = JSON.parse(JSON.stringify(this.puzzle));
          })
          .then(() => {
            this.notes = this.solution.map(() => { return {}; });
          })
          .then(() => {
            historyManager.reset();
            this.saveStateToHistory();
          })
          .finally(() => {
            this.loading = false;
          })
      },

      setPuzzleIndexValue(index, value) {
        value = value || this.selectedValue;

        if (this.notesMode && !String(value)) {
          this.$set(this.notes, index, {});
          return;
        }

        if (this.notesMode) {
          var currentValue = !!this.notes[index][value];
          this.$set(this.notes[index], value, !currentValue);
          return;
        }

        if (this.puzzle[index] == value) {
          value = "";
        }

        this.$set(this.puzzle, index, String(value));
        this.$set(this.notes, index, {});
      },

      setNotesIndexValue(index, value) {
        value = value || this.selectedValue;

        var currentValue = !!this.notes[index][value];
        this.$set(this.notes[index], value, !currentValue);
      },

      saveStateToHistory() {
        historyManager.snapshot({puzzle: this.puzzle, notes: this.notes});
      },

      undo() {
        var state = historyManager.undo();
        if (state) {
          this.$set(this, "puzzle", state.puzzle);
          this.$set(this, "notes", state.notes);
        }
      },

      redo() {
        var state = historyManager.redo();
        if (state) {
          this.$set(this, "puzzle", state.puzzle);
          this.$set(this, "notes", state.notes);
        }
      }
    },

    mounted() {
      // TODO this.solution.length == 0 && this.$nextTick(this.newGame);
    }
  }
</script>

<style lang="scss" scoped>
  .sudoku {
    display: grid;
    grid-template-columns: 580px 1fr;
    grid-gap: 10px;
    grid-template-areas:
      "header header"
      "board menu";
  }

  .sudoku__header {
    grid-area: header;
  }

  .sudoku__board {
    grid-area: board;
    display: grid;
    grid-template-columns: repeat(9, 60px);
    grid-template-rows: repeat(9, 60px);
    grid-gap: 5px;
    overflow: hidden;
  }

  .sudoku__menu {
    grid-area: menu;
  }

  .sudoku__cell {
    transition: color 0.3s, background 0.3s;
  }

  .sudoku__cell:after {
    content: " ";
    position: absolute;
    background: #607D8B;
    z-index: 1;
  }

  .sudoku__cell:nth-of-type(39):after,
  .sudoku__cell:nth-of-type(42):after,
  {
    width: 2px;
    height: calc(1200%);
    right: -5px;
    top: -600%;
  }

  .sudoku__cell:nth-of-type(23):after,
  .sudoku__cell:nth-of-type(50):after,
  {
    height: 2px;
    width: calc(1200%);
    bottom: -5px;
    left: -600%;
  }

  .sudoku__select-value {
    display: grid;
    grid-template-columns: repeat(11, 60px);
    grid-template-rows: 60px;
    text-align: center;
  }
</style>
