<template>
  <div class="card" :class="selected ? 'card--selected' : ''" v-on:click="$emit('toggle')">
    <svg viewBox="-2 -2 54 104" v-for="(c, i) in count" :key="i">
      <path :d="d" :stroke="stroke" :fill="fill"></path>
    </svg>
  </div>
</template>

<script>
  export default {
    props: ["color", "count", "shape", "pattern", "selected"],

    computed: {
      d() {
        return {
          diamond: "M25 0 L50 50 L25 100 L0 50 Z",
          squiggle: "M38.4,63.4c0,16.1,11,19.9,10.6,28.3c-0.5,9.2-21.1,12.2-33.4,3.8s-15.8-21.2-9.3-38c3.7-7.5,4.9-14,4.8-20 c0-16.1-11-19.9-10.6-28.3C1,0.1,21.6-3,33.9,5.5s15.8,21.2,9.3,38C40.4,50.6,38.5,57.4,38.4,63.4z",
          oval: "M25,99.5C14.2,99.5,5.5,90.8,5.5,80V20C5.5,9.2,14.2,0.5,25,0.5S44.5,9.2,44.5,20v60 C44.5,90.8,35.8,99.5,25,99.5z",
        }[this.shape];
      },

      stroke() {
        return {
          red: '#e74c3c',
          green: '#27ae60',
          purple: '#8e44ad'
        }[this.color];
      },

      fill() {
        if (this.pattern == "lines") {
          return `url(#striped-${this.color})`;
        }
        if (this.pattern == "filled") {
          return this.stroke;
        }
        return "none";
      },
    },
  }
</script>

<style lang="scss" scoped>
  .card {
    max-width: 200px;
    background: #fff;
    padding: 10px;
    border-radius: 3px;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.2s;
  }

  .card--selected {
    background: yellow;
  }

  svg {
    height: 80px;
  }

  path {
    stroke-width: 5px;
  }
</style>
