<template>
  <div>
    <template v-if="friendlyLabel">
      {{ friendlyLabel }}

      <v-icon>mdi-timer</v-icon>
    </template>
  </div>
</template>

<script>
export default {
  props: {
    refreshInterval: {
      type: Number,
      default: 100
    }
  },

  data() {
    return {
      startTime: undefined,
      stopTime: undefined,
      currentTime: undefined,
      interval: undefined
    };
  },

  computed: {
    friendlyLabel() {
      var ms = (this.stopTime || this.currentTime) - this.startTime;

      if (isNaN(ms)) {
        return;
      }

      var seconds = Math.floor(ms / 1000);

      var minutes = Math.floor(seconds / 60);

      if (minutes > 0) {
        seconds = minutes % 60;
      }

      return ("00" + minutes).substr(-2) + ":" + ("00" + seconds).substr(-2);
    }
  },

  methods: {
    start() {
      this.startTime = new Date();

      this.currentTime = new Date();

      this.interval = setInterval(this._updateCurrentTime, this.refreshInterval);
    },

    stop() {
      clearInterval(this.interval);

      this.stopTime = new Date();
    },

    reset() {
      this.startTime = undefined;
      this.stopTime = undefined;
    },

    _updateCurrentTime() {
      this.currentTime = new Date();
    }
  }
};
</script>
