<template>
  <div>
    <input type="file" @change="onChange" accept=".csv,text/plain" ref="input" :multiple="multiple">
  </div>
</template>

<script>
  import Papa from 'papaparse';

  export default {
    props: {
      value: {},

      multiple: {
        type: Boolean,
        defualt: false
      }
    },

    data: function() {
      return {
        fileReader: new FileReader(),
        isDragging: false,
      }
    },

    watch: {
      value: function() {
        if (!this.value) {
          this.$refs.input.value = "";
        }
      }
    },

    methods: {
      onChange: function(e) {
        var self = this;

        if (this.multiple) {
          e.target.files.forEach(f => {
            Papa.parse(f, {
              complete(result) {
                self.$emit("ready", {name: f.name, data: result.data});
              }
            });
          });

          return;
        }

        this.setFile(e.target.files[0]);
      },

      setFile: function(file) {
        if (!file) {
          return;
        }

        this.file = file;
        this.fileReader.readAsText(file);
      },

      onLoadEnd: function() {
        var self = this;

        Papa.parse(this.file, {
          complete(result) {
            self.$emit("input", result.data);
          }
        });
      }
    },

    mounted: function() {
      this.fileReader.onloadend = this.onLoadEnd;
    }
  }
</script>
