<template>
  <v-container>
    <v-row>
      <v-col cols="12">
        <div class="title">Input</div>

        <v-textarea filled outlined v-model="input" :hide-details="true"></v-textarea>
      </v-col>

      <v-col cols="12">
        <div class="title">Functions</div>

        <fns v-model="fns" :text-input="input" v-on:update:output="output = $event"></fns>
      </v-col>

      <v-col cols="11">
        <div class="title">Output</div>

        <v-textarea filled outlined :hide-details="true" :value="output"></v-textarea>
      </v-col>
      <v-col cols="1">
        <v-fade-transition mode="out-in">
          <v-btn
            id="copy-button"
            :color="copyButtonColor"
            v-clipboard:copy="output"
            v-clipboard:success="onCopySuccess"
            v-clipboard:error="onCopyError"
            :key="copySuccess + '_' + copyFailure"
          >
            <v-icon>
              <template v-if="copyFailure">mdi-thumb-down</template>
              <template v-else-if="copySuccess">mdi-thumb-up</template>
              <template v-else>mdi-content-copy</template>
            </v-icon>
          </v-btn>
        </v-fade-transition>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
  import fns from './Fns.vue';

  export default {
    components: {
      fns: fns
    },

    data() {
      return {
        input: "",
        output: "",

        fns: [
          {fnString: "replace", argsString: `/\\r/g, ""`},
          {fnString: "split", argsString: "/\\n/"},
          {fnString: "map", argsString: `(value, index) => {return value + ",";}`},
          {fnString: "join", argsString: `"\\n"`},
        ],

        copySuccess: false,
        copyFailure: false,
      };
    },

    computed: {
      copyButtonColor() {
        if (this.copyFailure) {
          return "red";
        }

        if (this.copySuccess) {
          return "green";
        }

        return "blue";
      },
    },

    watch: {
      copySuccess() {
        var self = this;
        self.copySuccess && setTimeout(() => { self.copySuccess = false; }, 2000);
      },

      copyFailure() {
        var self = this;
        self.copyFailure && setTimeout(() => { self.copyFailure = false; }, 2000);
      },
    },

    methods: {
      onCopySuccess() {
        this.copySuccess = true;
      },

      onCopyError(e) {
        console.error("error copying output", e);
        this.copyFailure = true;
      }
    },

    mounted() {
      this.input = "Type\nsome\ntext\nhere";
    }
  };
</script>
