    background: teal;
  }

  .solitaire__foundation__card {
    position: absolute;
  }

  .solitaire__tableau__card {
    position: relative;
  }

    @for $i from 2 through 13 {
      .solitaire__tableau__card:nth-child(#{$i}) {
        top: ($i * -175px) + 175px + ($i * 30px);
      }
    }

  .solitaire__tableaus {
    grid-area: tableau;
    display: flex;
    justify-content: space-between;
