<template>
  <button class="cell" :class="classes" :disabled="_disabled">
    <v-icon v-if="Boolean(icon)" :color="icon.color" :large="icon.large" :key="value">
      {{ icon.name }}
    </v-icon>
  </button>
</template>

<script>
export default {
  props: ["value", "api", "disabled"],

  computed: {
    _disabled() {
      if (this.value == this.api.constants.MINE || Boolean(this.disabled)) {
        return true;
      }

      return false;
    },

    icon() {
      return {
        "?": { name: "mdi-flag" },
        "??": { color: "brown", name: "mdi-help-circle" },
        "!": { name: "mdi-mine" },
        "1": { color: "cyan", large: true, name: "mdi-numeric-1" },
        "2": { color: "green", large: true, name: "mdi-numeric-2" },
        "3": { color: "red", large: true, name: "mdi-numeric-3" },
        "4": { color: "orange", large: true, name: "mdi-numeric-4" },
        "5": { color: "indigo", large: true, name: "mdi-numeric-5" },
        "6": { color: "pink", large: true, name: "mdi-numeric-6" },
        "7": { color: "brown", large: true, name: "mdi-numeric-7" },
        "8": { color: "black", large: true, name: "mdi-numeric-8" },
      }[this.value];
    },

    classes() {
      var classes = {
        'blue': String(this.value).length == 0,
        'cell--clickable': String(this.value).length == 0 && !this.disabled,
      };

      if (String(this.value).length > 0) {
        if (this.value == this.api.constants.FLAGGED_AS_MINE) {
          classes["green"] = true;
        } else if (this.value == this.api.constants.FLAGGED_AS_POSSIBLE_MINE) {
          classes["yellow"] = true;
        } else if (this.value == this.api.constants.MINE) {
          classes["red"] = true;
        } else {
          classes['blue lighten-4'] = true;
        }
      }

      return classes;
    }
  }
};
</script>

<style>
  .cell {
    display: inline-block;
    border-radius: 2px;
    margin: 1px;
    user-select: none;
  }

  .cell--clickable:hover {
    opacity: 0.7;
  }
</style>
