<script>
  export default {
    props: {
      suit: {
        type: String,
        required: true,
      },

      value: {
        type: String,
        required: true
      },

      faceup: {
        type: Boolean,
        default: true
      }
    },

    data() {
      return {
      };
    },

    computed: {
      suitIcon() {
        return "mdi-cards-" + {
          c: "club",
          h: "heart",
          s: "spade",
          d: "diamond",
        }[this.suit.toLowerCase().substr(0, 1)];
      },

      valueIcon() {
        if (this.value == "10") {
          return "mdi-numeric-10-box";
        }

        return "mdi-" + {
          a: "alpha-a",
          2: "numeric-2",
          3: "numeric-3",
          4: "numeric-4",
          5: "numeric-5",
          6: "numeric-6",
          7: "numeric-7",
          8: "numeric-8",
          9: "numeric-9",
          j: "alpha-j",
          q: "alpha-q",
          k: "alpha-k",
        }[this.value.toLowerCase().substr(0, 1)] + "-box";
      },

      iconColor() {
        return {
          c: "black",
          h: "red",
          s: "black",
          d: "red",
        }[this.suit.toLowerCase().substr(0, 1)];

      },
    },

    methods: {
    },
  }
</script>

<template>
  <v-card
    class="card"
    :class="faceup ? 'card--faceup' : 'card--facedown'"
  >
    <template v-if="faceup">
      <div class="card__top">
        <v-icon :color="iconColor">{{ valueIcon }}</v-icon>
        <v-icon :color="iconColor">{{ suitIcon }}</v-icon>
      </div>

      <div class="card__middle">
        <v-icon :color="iconColor" large>{{ valueIcon }}</v-icon>
        <v-icon :color="iconColor" large>{{ suitIcon }}</v-icon>
      </div>

      <div class="card__bottom">
        <v-icon :color="iconColor">{{ valueIcon }}</v-icon>
        <v-icon :color="iconColor">{{ suitIcon }}</v-icon>
      </div>
    </template>
  </v-card>
</template>

<style lang="scss" scoped>
  .card {
    width: 125px;
    height: 175px;
  }

  .card--facedown {
    background: radial-gradient(circle, rgba(2,0,36,1) 0%, rgba(0,212,255,1) 100%);
  }

  .card--faceup {
    background-color: white;
    display: grid;
    grid-template-rows: auto 1fr auto;
    grid-template-areas:
      "top"
      "middle"
      "bottom";
  }

  .card__top {
    grid-area: top;
  }

  .card__middle {
    grid-area: middle;
    text-align: center;
    align-self: center;
  }

  .card__bottom {
    grid-area: bottom;
    text-align: right;
  }
</style>
